<template>
	<div class="root">
		<div class="root" id="list_head">
			<div class="root" id="u_head">
				<div id="list_img" @click="openListImg()">
					<img width="100%" height="100%" :src="listImg" />
					<div id="popular">{{listPop}}</div>
					<div id="share">{{shareCount}}</div>
				</div>
				<div id="list_des">
					<div id="list_title" @click="showText()">{{listTitle}}</div>
					<a href="javascript:void(0)" id="user_info">
						<img :src="userHead" width="30px" height="30px" />
						<div id="user_name">{{userName}}</div>
					</a>
				</div>
			</div>
		</div>
		<div class="list_text" v-show="showDes">
			<h3 class="u_sm_title">描述详情</h3>
			<p>{{listText}}</p>
			<h3 class="u_sm_title">更新时间</h3>
			<p>{{updateTime}}</p>
			<h3 class="u_sm_title">歌单标签</h3>
			<p>{{listTag}}</p>
		</div>
		<h3 class="u_sm_title">歌曲列表（歌曲数量：{{songsCount}}）</h3>
		<ul class="root" id="list_table">
			<div class="ball" v-if="listArr.length == 0"></div>
			<Song v-show="listArr.length != 0" v-for="(item, index) in listArr.length" :key="index">
				<span slot="song_name">{{listArr[index].name}}</span>
				<span slot="song_alia" v-if="listArr[index].alia.length > 0" class="alia">（{{listArr[index].alia[0]}}）</span>
				<i slot="sghot" class="sghot" v-show="listInfo[index].playMaxbr > 320000"></i>
				<span slot="song_artists" v-html="setArtists(index)"></span>
				<span slot="song_album">{{listArr[index].al.name}}</span>
				<div slot="play_btn" class="sgchfr" :value="listArr[index].id" :song="listArr[index].name" :artists="setArtists(index)" @click="toPlayer($event)"></div>
			</Song>
		</ul>
		<div id="more_tip" v-show="listArr.length != 0"></div>
		<h3 class="u_sm_title">歌单评论（评论数量：{{cmtCount}}）</h3>
		<ul class="root" id="list_cmt_board">
			<Comments v-for="(item, index) in listCmts.length" :key="index">
				<img :src="listCmts[index].user.avatarUrl" width="100%" height="100%" slot="head_img" />
				<a href="javascript:void(0)" slot="username" class="username" v-html="listCmts[index].user.nickname"></a>
				<div slot="cmt_time" class="cmt_time" v-html="setCmtTime(listCmts[index].time)"></div>
				<a href="javascript:void(0)" class="like" v-html="setLikes(listCmts[index].likedCount)" slot="like" @click="addLike($event)"></a>
				<p slot="comment" class="cmt_text" v-html="listCmts[index].content"></p>
			</Comments>
		</ul>
		<a href="#" id="cmt_tip"></a>
	</div>
</template>

<script>
	import Song from "../components/Song.vue";
	import Comments from "../components/Comments.vue";
	import axios from "../axios";
	export default {
		name: "Detail",
		data() {
			return {
				listArr: [],
				listInfo: [],
				listID: "",
				showDes: false,
				listImg: "../assets/default_img.png",
				listPop: "歌单热度",
				shareCount: "👁‍🗨 0",
				listTitle: "【Title】Failed to get data!",
				listText: "【Describe】The API interface looks like it's not working normally when you see nothing here.",
				userHead: "../assets/default_img.png",
				userName: "User",
				updateTime: "未知",
				listTag: "无",
				songsCount: 0,
				listCmts: [],
				cmtCount: 0
			}
		},
		mounted() {
			axios.get('http://music.eleuu.com/playlist/detail?id=' + this.$root.$children[0].listID).then((res) => {
			 	console.log("歌单详细信息：", res);
			  	this.setAllInfo(res)
			}).catch((err) => {
			 	 console.log(err)
			});  //获取歌单详细信息
			axios.get('http://music.eleuu.com/comment/playlist?id=' + this.$root.$children[0].listID).then((res) => {
			  	console.log("歌单评论：", res);
				this.cmtCount = res.data.total;
				if (res.data.hotComments.length != 0) {
					this.listCmts = res.data.hotComments
				} else {
					this.listCmts = res.data.comments
				}
			}).catch((err) => {
			 	 console.log(err)
			})  //获取歌单评论
		},
		methods: {
			setCmtTime(t) {
				let d = new Date(t);
				return d.getFullYear() + "年" + (d.getMonth() + 1) + "月" + d.getDate() + "日"
			},
			addLike(e) {
				let count = e.target.innerHTML;
				if (/\./.test(count) == false) {
					e.target.innerHTML = parseInt(count) + 1 + " " + "👍"
				}
			},
			setLikes(co) {
				if (parseInt(co) > 100000000) {
					co = (co / 100000000).toFixed(1) + "亿" + " " + "👍"
				} else if (parseInt(co) > 10000) {
					co = (co / 10000).toFixed(1) + "万" + " " + "👍"
				} else {
					co = co + " " + "👍"
				};
				return co
			},
			setAllInfo(res) {
				this.listArr = res.data.playlist.tracks;
				this.listID = res.data.playlist.id;
				this.listImg = res.data.playlist.coverImgUrl;
				this.listTitle = res.data.playlist.name;
				this.listText = res.data.playlist.description;
				this.setTruePop(res);
				this.shareCount = "👁‍🗨" + " " + res.data.playlist.shareCount;
				this.userHead = res.data.playlist.creator.avatarUrl;
				this.userName = res.data.playlist.creator.nickname;
				this.listInfo = res.data.privileges,
				this.updateTime = this.setUpdateTime(res.data.playlist.updateTime);
				this.listTag = this.setListTag(res.data.playlist.tags);
				this.songsCount = res.data.playlist.trackCount
			},
			setTruePop(res) {
				let countNum = res.data.playlist.playCount;
				if (parseInt(countNum) > 100000000) {
					countNum = (countNum / 100000000).toFixed(1) + "亿"
				} else if (parseInt(countNum) > 10000) {
					countNum = (countNum / 10000).toFixed(1) + "万"
				};
				this.listPop = countNum
			},
			setListTag(tags) {
				if (tags.length == 0) {
					return "无"
				} else if (tags.length == 1) {
					return tags[0]
				} else if (tags.length > 1) {
					let setTags = "";
					for (let i = 0; i < tags.length; i++) {
						if (i != tags.length - 1) {
							setTags = setTags + tags[i] + "、"
						} else if (i == tags.length - 1) {
							setTags = setTags + tags[i]
						}
					};
					return setTags
				}
			},
			setArtists(index) {
				if (this.listArr[index].ar.length == 1) {
					return this.listArr[index].ar[0].name
				} else if (this.listArr[index].ar.length > 1) {
					let thisName = "";
					for (let i = 0; i < this.listArr[index].ar.length; i++) {
						if (i != this.listArr[index].ar.length - 1) {
							thisName = thisName + this.listArr[index].ar[i].name + "、"
						} else if (i == this.listArr[index].ar.length - 1) {
							thisName = thisName + this.listArr[index].ar[i].name
						}
					};
					return thisName
				}
			},
			setUpdateTime(t) {
				let d = new Date(t);
				return d.getFullYear() + "年" + (d.getMonth() + 1) + "月" + d.getDate() + "日"
			},
			openListImg() {
				window.open(this.listImg)
			},
			showText() {
				this.showDes = !this.showDes
			},
			toPlayer(e) {
				this.$root.$children[0].checkSame(e.target.getAttribute("value"), e.target.getAttribute("artists"), e.target.getAttribute("song"));
				// this.$root.$children[0].musicID = e.target.getAttribute("value");
				// this.$root.$children[0].waitPlaying.push({songName: e.target.getAttribute("song"), artists: e.target.getAttribute("artists"), songID: e.target.getAttribute("value")});
				// this.$root.$children[0].musicSrc = "https://music.163.com/song/media/outer/url?id=" + e.target.getAttribute("value") + ".mp3";
				// if (this.$root.$children[0].waitPlaying.length > 0) {
				// 	this.$root.$children[0].playingIndex = this.$root.$children[0].waitPlaying.length - 1
				// };
				this.$router.replace("/player")
			}
		},
		components: {
			Song,
			Comments
		}
	}
</script>

<style scoped>
    @import "../css/Detail.css";
</style>