<template>
	<div id="app">
		<audio hidden="hidden" ref="music" autoplay :src="musicSrc" id="music"></audio>
		<div class="root" id="board">
			<div id="topbar-fixed">
				<div class="topbar">
					<router-link active-class="active" tag="button" replace to="/player" id="player_btn" @click="toPlayer()">播放器 🎵</router-link>
				</div>
				<ul id="u-tab">
					<!-- tag属性可以决定router-link在html中最终解析成什么标签 -->
					<!-- replace属性可以使页面在切换后不可返回 -->
					<!-- active-class属性可设置处于活跃（显示）状态时的类名（不推荐，可在index.js中改） -->
					<router-link active-class="active" tag="li" replace class="tab-change" to="/home">{{rcmSongs}}</router-link>
					<router-link active-class="active" tag="li" replace class="tab-change" to="/hot">{{hotSongs}}</router-link>
					<router-link active-class="active" tag="li" replace class="tab-change" to="/search">{{searchSongs}}</router-link>
				</ul>
			</div>
		</div>
		<router-view></router-view>
	</div>
</template>

<script>
	export default {
		name: 'App',
		data() {
			return {
				rcmSongs: "推荐音乐",
				hotSongs: "热歌榜",
				searchSongs: "搜索",
				playerShow: false,
				isPlaying: false,
				musicID: "",
				listID: "",
				musicSrc: "",
				waitPlaying: [],
				playingIndex: 0
			}
		},
		mounted() {
			document.title = "Light Music"
		},
		methods: {
			toPlayer() {
				console.log("音乐ID：" + this.$root.$children[0].listID)
			},
			checkSame(id, artists, song) {
				let isAdd = true;
				let nowIndex = 0;
				for (let i = 0; i < this.waitPlaying.length; i++) {
					if (id == this.waitPlaying[i].songID) {
						isAdd = false;
						nowIndex = i
					}
				};
				this.musicID = id;
				if (isAdd == true) {
					this.waitPlaying.push({songName: song, artists: artists, songID: id});
					if (this.waitPlaying.length > 0) {
						this.playingIndex = this.waitPlaying.length - 1
					} else if (this.waitPlaying.length == 0) {
						this.playingIndex = 0
					}
				};
				this.musicSrc = "https://music.163.com/song/media/outer/url?id=" + id + ".mp3";
				this.playingIndex = nowIndex
			}
		}
	}
</script>

