<template>
	<div class="root">
		<div id="hot_top_bg" class="root">
			<button id="top_btn">2077</button>
		</div>
		<ul class="root" id="hot_songs">
			<div class="ball" v-show="songsData.length == 0"></div>
			<Song v-for="(item, index) in songsData.length" v-show="songsData.length != 0 && index < listNum" :value="songsData[index].id" :key="index">
				<span slot="song_name">{{songsData[index].name}}</span>
				<span slot="song_alia" v-if="songsData[index].alia.length > 0" class="alia">（{{songsData[index].alia[0]}}）</span>
				<i slot="sghot" class="sghot" v-if="songsData[index].h.br > 320000"></i>
				<span slot="song_artists" v-html="setArtists(index)"></span>
				<span slot="song_album">{{songsData[index].al.name}}</span>
				<div slot="play_btn" class="sgchfr" :value="songsData[index].id" :song="songsData[index].name" :artists="setArtists(index)" @click="toPlayer($event)"></div>
			</Song>
		</ul>
		<a href="javascript:void(0)" id="load_more" v-if="songsData.length > 0" @click="loadMore()">⏬ 点击此处加载更多 ⏬</a>
		<div id="head_pic" @click="openHP"></div>
	</div>
</template>

<script>
	import Song from "../components/Song.vue";
	import axios from "../axios";
	export default {
		name: "Hot",
		data() {
			return {
				songName: "歌名",
				listNum: 20,
				isAlia: true,
				alia: "别名",
				isSQ: true,
				artists: "歌手",
				album: "专辑",
				songsData: []
			}
		},
		mounted() {
			this.getHotSongs()
		},
		methods: {
			openHP() {
				document.documentElement.scrollTop = 0
			},
			loadMore() {
				if (this.listNum != this.songsData.length) {
					this.listNum = this.listNum + 20
				} else if (this.listNum >= this.songsData.length) {
					alert("😅差不多得了...")
				}
			},
			getHotSongs() {
				axios.get('http://music.eleuu.com/playlist/detail?id=3778678').then((res) => {
					console.log("热歌：", res);
					this.setAllsongsInfo(res)
				}).catch((err) => {
					console.log(err)
				})  //获取若干首热门歌曲
			},
			setAllsongsInfo(res) {
				this.songsData = res.data.playlist.tracks
			},
			setArtists(index) {
				if (this.songsData[index].ar.length == 1) {
					return this.songsData[index].ar[0].name
				} else if (this.songsData[index].ar.length > 1) {
					let thisName = "";
					for (let i = 0; i < this.songsData[index].ar.length; i++) {
						if (i != this.songsData[index].ar.length - 1) {
							thisName = thisName + this.songsData[index].ar[i].name + "、"
						} else if (i == this.songsData[index].ar.length - 1) {
							thisName = thisName + this.songsData[index].ar[i].name
						}
					};
					return thisName
				}
			},
			toPlayer(e) {
				this.$root.$children[0].checkSame(e.target.getAttribute("value"), e.target.getAttribute("artists"), e.target.getAttribute("song"));
				this.$router.replace("/player")
			}
		},
		components: {
			Song
		}
	}
</script>

<style scoped>
    @import "../css/Hot.css";
</style>