<template>
	<div class="root" id="search_board">
		<div id="m_input">
			<input type="search" id="search" placeholder="搜索歌曲、歌手、专辑..." ref="input" @keypress.enter="getSearchSongs()" @input="inputShow()" />
		</div>
		<div class="root" id="sug_search" v-show="wordsShow">
			<ul class="root" id="sug_words">
				<div class="ball" v-show="sugWords.length == 0"></div>
				<li class="sug_words" v-for="(iten, index) in sugWords.length" v-html="sugWords[index].first" @click="inputWord($event)" :key="index">搜索关键词</li>
			</ul>
		</div>
		<ul class="root" id="m_song_list" v-show="resultShow">
			<Song v-for="(item, index) in searchResults.length" v-show="index < 30" :value="searchResults[index].id" :key="index">
				<span slot="song_name">{{searchResults[index].name}}</span>
				<span slot="song_alia" v-if="searchResults[index].alias.length > 0" class="alia">（{{searchResults[index].alias[0]}}）</span>
				<span slot="song_artists" v-html="setSongsInfo(index)"></span>
				<span slot="song_album">{{searchResults[index].album.name}}</span>
				<div slot="play_btn" class="sgchfr" :value="searchResults[index].id" @click="toPlayer($event)" :song="searchResults[index].name" :artists="setSongsInfo(index)"></div>
			</Song>
		</ul>
		<div id="search_tip" v-show="resultShow == false"></div>
	</div>
</template>

<script>
	import Song from "../components/Song.vue";
	import axios from "../axios";
	export default {
		name: "Search",
		data() {
			return {
				wordsShow: true,
				resultShow: false,
				sugWords: [],
				searchResults: []
			}
		},
		mounted: function() {
			this.getDefaultWords()
		},
		methods: {
			getDefaultWords() {
				axios.get('http://music.eleuu.com/search/hot').then((res) => {
				  console.log("搜索关键词：", res);
				  this.sugWords = res.data.result.hots
				}).catch((err) => {
				  console.log(err)
				})  //获取默认搜索关键词
			},
			getSearchSongs() {
				axios.get('http://music.eleuu.com/search?keywords=' + this.$refs.input.value).then((res) => {
				  console.log("搜索结果：", res);
				  this.searchResults = res.data.result.songs;
				  this.resultShow = true
				}).catch((err) => {
				  console.log(err)
				})  //获取搜索结果
			},
			inputShow() {
				if (this.$refs.input.value.length > 0) {
					this.wordsShow = false
				} else if (this.$refs.input.value.length == 0) {
					this.wordsShow = true;
					this.resultShow = false
				}
			},  //调整界面显示
			setSongsInfo(index) {
				if (this.searchResults[index].artists.length == 1) {
					return this.searchResults[index].artists[0].name
				} else if (this.searchResults[index].artists.length > 1) {
					let thisName = "";
					for (let i = 0; i < this.searchResults[index].artists.length; i++) {
						if (i != this.searchResults[index].artists.length - 1) {
							thisName = thisName + this.searchResults[index].artists[i].name + "、"
						} else if (i == this.searchResults[index].artists.length - 1) {
							thisName = thisName + this.searchResults[index].artists[i].name
						}
					};
					return thisName
				}
			},
			toPlayer(e) {
				console.log("音乐ID：" + this.$root.$children[0].musicID);
				this.$root.$children[0].checkSame(e.target.getAttribute("value"), e.target.getAttribute("artists"), e.target.getAttribute("song"));
				this.$router.replace("/player")
			},
			inputWord(e) {
				document.getElementById("search").value = e.target.innerHTML;
				axios.get('http://music.eleuu.com/search?keywords=' + e.target.innerHTML).then((res) => {
					console.log("搜索结果：", res);
					this.searchResults = res.data.result.songs;
					this.resultShow = true;
					this.wordsShow = false
				}).catch((err) => {
					console.log(err)
				})
			}
		},
		components: {
			Song
		}
	}
</script>

<style scoped>
    @import "../css/Search.css";
</style>