<template>
	<li class="songs_list1" @click="toDetail($event)">
		<div class="list_img">
			<slot name="list_img_url"></slot>
			<slot name="list_popular"></slot>
		</div>
		<slot name="title_name"></slot>
	</li>
</template>

<script>
	export default {
		name: "List",
		data() {
			return {
				defaultImgUrl: "../asets/default_img.png",
				listName: "歌单描述..."
			}
		},
		methods: {
			toDetail(e) {
				this.$root.$children[0].listID = e.target.getAttribute("value");
				this.$router.replace("/detail")
			}
		}
	}
</script>

<style scoped>
    @import "../css/List.css";
</style>