<template>
	<div class="root">
		<div class="root" id="banner_frame">
			<ul id="banner" class="root" :style="'width:' + (100 * banners.length) + 'vw; transform:translateX(' + imgIndex * -10 +'%)'">
				<li v-for="(item, index) in banners.length" @click.stop="openUrl(index)" :key="index">
					<img :src="banners[index].pic" width="100%" height="100%" />
				</li>
			</ul>
			<button id="privious_img" @click="priviousImg()"></button>
			<button id="next_img" @click="nextImg()"></button>
		</div>
		<div class="root" id="rcm_board">
			<div class="remd_tl">推荐歌单</div>
			<ul class="root" id="list_board">
				<div class="ball" v-if="rcmLists.length == 0"></div>
				<List v-for="(item, index) in rcmLists" :songlist="rcmLists[index]" v-show="index < rcmNum" :key="index">
					<img :src="rcmLists[index].picUrl" :value="rcmLists[index].id" v-if="index < rcmNum" width="100%" height="100%" slot="list_img_url" />
					<div class="u_earp" slot="list_popular" v-html="setCount(index)"></div>
					<p class="list_tl" slot="title_name" :value="rcmLists[index].id">{{rcmLists[index].name}}</p>
				</List>
			</ul>
			<a href="javascript:void(0)" class="load_more" @click="loadMoreRcm()">🔽 点击加载更多歌单 🔽</a>
		</div>
		<div class="root" id="new_board">
			<div class="remd_tl">最新音乐</div>
			<ul class="root" id="new_songs">
				<div class="ball" v-if="newSongs.length == 0"></div>
				<Song v-show="newSongs.length != 0 && index < newNum" v-for="(item, index) in newSongs.length" :key="index">
					<span slot="song_name">{{newSongs[index].name}}</span>
					<span slot="song_alia" v-if="newSongs[index].alia.length > 0" class="alia">（{{newSongs[index].alia[0]}}）</span>
					<!-- <i slot="sghot" class="sghot" v-if="newSongs[index].h.br > 320000"></i> -->
					<i slot="sghot" class="sghot"></i>
					<span slot="song_artists" v-html="setArtists(index)"></span>
					<span slot="song_album">{{newSongs[index].al.name}}</span>
					<div slot="play_btn" class="sgchfr" :value="newSongs[index].id" :song="newSongs[index].name" :artists="setArtists(index)" @click="toPlayer($event)"></div>
				</Song>
			</ul>
			<a href="javascript:void(0)" class="load_more" @click="loadMoreSongs()">🔽 点击加载更多歌曲 🔽</a>
		</div>
		<div id="bottom_pic"></div>
	</div>
</template>

<script>
	import List from "../components/List.vue";
	import Song from "../components/Song.vue";
	import axios from "../axios";

	export default {
		name: "Home",
		data() {
			return {
				banners: [],
				imgIndex: 0,
				imgTimer: undefined,
				songName: "歌名",
				isAlia: true,
				alia: "别名",
				isSQ: true,
				artists: "歌手",
				album: "专辑",
				rcmNum: 6,
				rcmLists: [],
				newNum: 10,
				newSongs: []
			}
		},
		mounted() {
			const that = this;
			that.getRcmLists();
			that.getNewSongs();
			axios.get('http://music.eleuu.com/banner?type=1').then((res) => {
			  	console.log("轮播图：", res);
				that.banners = res.data.banners
			}).catch((err) => {
			  	console.log(err)
			});  //获取轮播图（图片比例为18:7）
			this.imgTimer = setInterval(() => {
				this.nextImg()
			}, 4000)
		},
		methods: {
			openUrl(index) {
				if (this.banners[index].url != "" && this.banners[index].url != null) {
					window.open(this.banners[index].url)
				}
			},
			priviousImg() {
				if (this.imgTimer) {
					clearInterval(this.imgTimer)
				};
				if (this.imgIndex == 0) {
					this.imgIndex = this.banners.length - 1
				} else {
					this.imgIndex = this.imgIndex - 1
				};
				this.imgTimer = setInterval(() => {
					this.nextImg()
				}, 4000)
			},
			nextImg() {
				if (this.imgTimer) {
					clearInterval(this.imgTimer)
				};
				if (this.imgIndex == this.banners.length - 1) {
					this.imgIndex = 0
				} else {
					this.imgIndex = this.imgIndex + 1
				};
				this.imgTimer = setInterval(() => {
					this.nextImg()
				}, 4000)
			},
			loadMoreRcm() {
				if (this.rcmNum < this.rcmLists.length) {
					this.rcmNum = this.rcmNum + 6
				} else if (this.rcmNum >= this.rcmLists.length) {
					alert("没有更多了，😅差不多得了...")
				}
			},
			loadMoreSongs() {
				if (this.newNum < this.newSongs.length) {
					this.newNum = this.newNum + 10
				} else if (this.newNum >= this.newSongs.length) {
					alert("没有更多了，😅差不多得了...")
				}
			},
			setCount(index) {
				let popCount = this.rcmLists[index].playCount;
				if (parseInt(popCount) > 100000000) {
					popCount = (popCount / 100000000).toFixed(1) + "亿"
				} else if (parseInt(popCount) > 10000) {
					popCount = (popCount / 10000).toFixed(1) + "万"
				};
				return popCount
			},
			setArtists(index) {
				if (this.newSongs[index].ar.length == 1) {
					return this.newSongs[index].ar[0].name
				} else if (this.newSongs[index].ar.length > 1) {
					let thisName = "";
					for (let i = 0; i < this.newSongs[index].ar.length; i++) {
						if (i != this.newSongs[index].ar.length - 1) {
							thisName = thisName + this.newSongs[index].ar[i].name + "、"
						} else if (i == this.newSongs[index].ar.length - 1) {
							thisName = thisName + this.newSongs[index].ar[i].name
						}
					};
					return thisName
				}
			},
			getRcmLists() {
				const that = this;
				axios.get('http://music.eleuu.com/personalized?limit=100').then((res) => {
				  	console.log("推荐歌单：", res);
				  	that.rcmLists = res.data.result
				}).catch((err) => {
				  	console.log(err)
				})  //获取推荐歌单
				// http://musicapi.leanapp.cn/
				// http://music.eleuu.com/
			},
			getNewSongs() {
				const that = this;
				axios.get('http://music.eleuu.com/playlist/detail?id=3779629').then((res) => {
					  console.log("新歌：", res);
				 	 that.newSongs = res.data.playlist.tracks
				}).catch((err) => {
				  	console.log(err)
				})  //获取新歌
			},
			toPlayer(e) {
				this.$root.$children[0].checkSame(e.target.getAttribute("value"), e.target.getAttribute("artists"), e.target.getAttribute("song"));
				this.$router.replace("/player")
			}
		},
		components: {
			List,
			Song
		}
	}
</script>

<style scoped>
	@import "../css/Home.css";
</style>