<template>
	<div id="player_itf">
		<div class="itf_frame">
			<div id="m_wrap">
				<div id="album_img" @click="setLyricShow()" v-show="lyricShow == false">
					<img :src="albumImg" id="song_disc" width="70%" height="70%" />
				</div>
				<div id="lyric_board" class="root" @click="setLyricShow()" v-show="lyricShow == true">
					<ul id="lyric_frame" class="root">
						<li class="lyric_content" v-for="(item, index) in lyricContent.length" v-html="lyricContent[index].lyric" :key="index"></li>
					</ul>
				</div>
			</div>
			<div id="song_info">
				<h2 id="song_name">{{songName}}</h2>
				<div id="artist" v-html="artist"></div>
				<div id="song_prg">
					<div id="prg_left">{{currentSong}}</div>
					<div id="prg_center">
						<input type="range" id="progress" @touchstart="touchDown()" @touchend="touchUp()" @change="setPro()" value="0" max="100" />
					</div>
					<div id="prg_right">{{songLength}}</div>
				</div>
			</div>
			<div id="play_btns">
				<div id="volume_up" @click="volumeUp()">🔼</div>
				<div id="volume_down" @click="volumeDown()">🔽</div>
				<ul id="songs_change">
					<li @click="privSong()">⏪</li>
					<li @click="nextSong()">⏩</li>
				</ul>
				<button id="show_waiting" @click="listShow">列表</button>
				<div id="play_paused" @click="playPaused($event)">⏺</div>
			</div>
		</div>
		<div class="root">
			<div class="remd_tl">歌曲评论</div>
			<ul class="root" id="cmt_list">
				<div class="cmt_tip" v-if="songComments.length == 0"></div>
				<Comments v-for="(item, index) in songComments.length" :key="index">
					<img :src="songComments[index].user.avatarUrl" width="100%" height="100%" slot="head_img" />
					<a href="javascript:void(0)" slot="username" class="username" v-html="songComments[index].user.nickname"></a>
					<div slot="cmt_time" class="cmt_time" v-html="setCmtTime(index)"></div>
					<a href="javascript:void(0)" class="like" @click="addLikeCount($event)" slot="like" v-html="setLikeCount(index)"></a>
					<p slot="comment" class="cmt_text" v-html="songComments[index].content"></p>
				</Comments>
				<div id="more_cmt_tip" class="root" v-show="songComments.length != 0"></div>
			</ul>
		</div>
		<div id="wait_playing_itf" v-show="showWaiting">
			<div id="wait_playing_list">
				<ul id="waiting_list">
					<li class="waiting_item root" v-for="(item, index) in songsListWaiting" :value="songsListWaiting[index].songID" @click="changeThisSong(index)" :key="index">
						<span>{{songsListWaiting[index].artists}}</span>
						<span> - </span>
						<span class="font_weight">{{songsListWaiting[index].songName}}</span>
					</li>
					<div id="list_waiting_tip" class="root" v-show="songsListWaiting.length == 0"></div>
				</ul>
				<div id="remove_btns">
					<button @click="removeAllSongs">❌ 清空列表</button>
					<button @click="listHidden">❎ 关闭列表</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from "../axios";
	import Comments from "../components/Comments.vue";
	export default {
		name: "Player",
		data() {
			return {
				songName: "PLAYER",
				artist: "Light Music",
				currentSong: "00:00",
				songLength: "00:00",
				totalSong: 0,
				lyricContent: [{time: 0, lyric: "无歌词"}],
				albumImg: "http://mrw.so/636jLK",
				songComments: [],
				timer: undefined,
				lyricShow: false,
				showWaiting: false,
				songsListWaiting: this.$root.$children[0].waitPlaying
			}
		},
		mounted() {
			document.documentElement.scrollTop = 0;
			this.setThisAllInfo();
			if (document.getElementById("music").paused == false) {
				document.getElementById("play_paused").innerHTML = "⏸";
				document.getElementById("song_disc").style.animationPlayState = "running";
			};
			
			const that = this;
			this.timer = setInterval(() => {
				this.timerRunning()
			}, 1000);
		},
		methods: {
			setThisAllInfo() {
				setTimeout(() => {
					document.getElementById("song_disc").style.display = "block";
					if (document.getElementById("music").paused == false) {
						document.getElementById("play_paused").innerHTML = "⏸"
					}
				}, 750);
				document.getElementById("music").onended = function() {
					if (document.getElementById("song_disc")) {
						document.getElementById("song_disc").style.animationPlayState = "paused";
						document.getElementById("play_paused").innerHTML = "⏺"
					}
				};
				document.getElementById("song_disc").style.animationPlayState = "running";
				if (this.$root.$children[0].musicID != "") {
					axios.get('http://music.eleuu.com/song/detail?ids=' + this.$root.$children[0].musicID).then((res) => {
						console.log("歌曲详情：", res);
						this.setSongInfo(res);
					}).catch((err) => {
						console.log(err)
					});  //获取歌曲详情
					axios.get('http://music.eleuu.com/comment/music?id=' + this.$root.$children[0].musicID).then((res) => {
						console.log("歌曲评论：", res, "\n");
						if (res.data.hotComments.length != 0) {
							this.songComments = res.data.hotComments
						} else {
							this.songComments = res.data.comments
						}
					}).catch((err) => {
						console.log(err)
					});  //获取评论
					axios.get('http://music.eleuu.com/lyric?id=' + this.$root.$children[0].musicID).then((res) => {
						console.log("歌词：", res);
						this.setLyric(res.data)
					}).catch((err) => {
						console.log(err)
					})  //获取歌词
				}
			},
			changeThisSong(index) {
				if (index != this.$root.$children[0].playingIndex) {
					this.$root.$children[0].playingIndex = index;
					this.$root.$children[0].musicSrc = "https://music.163.com/song/media/outer/url?id=" + this.$root.$children[0].waitPlaying[index].songID + ".mp3";
					this.$root.$children[0].musicID = this.$root.$children[0].waitPlaying[index].songID;
					this.setThisAllInfo()
				} else if (index == this.$root.$children[0].playingIndex) {
					alert("当前正在播放该歌曲")
				}
			},
			privSong() {
				if (this.$root.$children[0].playingIndex != 0) {
					this.$root.$children[0].playingIndex = this.$root.$children[0].playingIndex - 1;
					this.$root.$children[0].musicSrc = "https://music.163.com/song/media/outer/url?id=" + this.$root.$children[0].waitPlaying[this.$root.$children[0].playingIndex].songID + ".mp3";
					this.$root.$children[0].musicID = this.$root.$children[0].waitPlaying[this.$root.$children[0].playingIndex].songID;
					this.setThisAllInfo()
				} else if (this.$root.$children[0].playingIndex == 0) {
					alert("当前已是列表第一首歌")
				}
			},
			nextSong() {
				if (this.$root.$children[0].playingIndex != this.$root.$children[0].waitPlaying.length - 1) {
					this.$root.$children[0].playingIndex = this.$root.$children[0].playingIndex + 1;
					this.$root.$children[0].musicSrc = "https://music.163.com/song/media/outer/url?id=" + this.$root.$children[0].waitPlaying[this.$root.$children[0].playingIndex].songID + ".mp3";
					this.$root.$children[0].musicID = this.$root.$children[0].waitPlaying[this.$root.$children[0].playingIndex].songID;
					this.setThisAllInfo()
				} else if (this.$root.$children[0].playingIndex == this.$root.$children[0].waitPlaying.length - 1) {
					alert("当前已是最后一首歌")
				}
			},
			setLyricShow() {
				this.lyricShow = !this.lyricShow
			},
			listShow() {
				this.showWaiting = true
			},
			listHidden() {
				this.showWaiting = false
			},
			removeAllSongs() {
				this.$root.$children[0].waitPlaying = [];
				this.songsListWaiting = []
			},
			timerRunning() {
				const that = this;
				let ct = document.getElementById("music").currentTime;
				if (ct < 60) {
					let act = parseInt(ct);
					if (act < 10) {
								act = "0" + act
					};
					ct = "00" + ":" + act;
				} else if (ct >= 60) {
					let min = parseInt(ct / 60);
					let sec = parseInt(ct % 60);
					if (min < 10) {
						min = "0" + min
					};
					if (sec < 10) {
						sec = "0" + sec
					};
					ct = min + ":" + sec
				};
				that.currentSong = ct;
				if (document.getElementById("progress") && document.getElementById("music").duration != 0) {
					document.getElementById("progress").value = parseInt(document.getElementById("music").currentTime / document.getElementById("music").duration * 100)
				}
			},
			touchDown() {
				if (this.timer) {
					clearInterval(this.timer)
				}
			},
			touchUp() {
				const that = this;
				that.timer = setInterval(() => {
					this.timerRunning()
				}, 1000);
			},
			setLyric(data) {
				if (data.lrc) {
					if (data.lrc.lyric) {
						let lyricArr = [];
						lyricArr = data.lrc.lyric.split("\n");
						lyricArr.pop();
						let lt = [];
						for (let i = 0; i < lyricArr.length; i++) {
							let t = (parseInt(lyricArr[i].slice(1,3)) * 60 + parseFloat(lyricArr[i].slice(4,10))).toFixed(3);
							lt.push(t)
						};
						let la = [];
						for (let i = 0; i < lyricArr.length; i++) {
							la.push(lyricArr[i].slice(11))
						};
						this.lyricContent = [];
						for (let i = 0; i < lt.length; i++) {
							this.lyricContent.push({time: lt[i], lyric: la[i]})
						}
					}
				}
			},
			setLikeCount(index) {
				let co = this.songComments[index].likedCount;
				if (parseInt(co) > 100000000) {
					co = (co / 100000000).toFixed(1) + "亿" + " " + "👍"
				} else if (parseInt(co) > 10000) {
					co = (co / 10000).toFixed(1) + "万" + " " + "👍"
				} else {
					co = co + " " + "👍"
				};
				return co
			},
			setCmtTime(index) {
				let ti = this.songComments[index].time;
				let getTi = new Date(ti);
				return getTi.getFullYear() + "年" + (getTi.getMonth() + 1) + "月" + getTi.getDate() + "日"
			},
			setPro() {
				if (this.totalSong != 0) {
					document.getElementById("music").currentTime = (document.getElementById("progress").value / 100) * (this.totalSong / 1000)
				}
			},
			volumeDown() {
				let vo = document.getElementById("music").volume;
				if (vo >= 0.2) {
					document.getElementById("music").volume = vo - 0.2
				} else if (vo < 0.2) {
					document.getElementById("music").volume = 0;
					alert("当前已静音")
				}
			},
			volumeUp() {
				let vo = document.getElementById("music").volume;
				if (vo <= 0.8) {
					document.getElementById("music").volume = vo + 0.2
				} else if (vo > 0.8) {
					document.getElementById("music").volume = 1;
					alert("当前已经是最大音量")
				}
			},
			addLikeCount(e) {
				let count = e.target.innerHTML;
				if (/\./.test(count) == false) {
					e.target.innerHTML = parseInt(count) + 1 + " " + "👍"
				}
			},
			playPaused(e) {
				if (document.getElementById("music").currentSrc != "") {
					if (e.target.innerHTML == "⏸") {
						document.getElementById("music").pause();
						document.getElementById("song_disc").style.animationPlayState = "paused";
						this.$root.$children[0].isPlaying = false;
						e.target.innerHTML = "⏺"
					} else if (e.target.innerHTML == "⏺") {
						document.getElementById("music").play();
						document.getElementById("song_disc").style.animationPlayState = "running";
						this.$root.$children[0].isPlaying = true;
						e.target.innerHTML = "⏸"
					}
				} else if (document.getElementById("music").currentSrc == "") {
					alert("当前无音频src")
				}
			},
			setSongInfo(res) {
				this.songName = res.data.songs[0].name;
				this.albumImg = res.data.songs[0].al.picUrl;
				this.artist = this.setArtists(res.data.songs[0].ar);
				this.songLength = this.setLength(res.data.songs[0].dt);
				this.totalSong = res.data.songs[0].dt
			},
			setArtists(res) {
				if (res.length == 1) {
					return res[0].name
				} else if (res.length > 1) {
					let arName = "";
					for (let i = 0; i < res.length; i++) {
						if (i != res.length - 1) {
							arName = arName + res[i].name + "、"
						} else if (i == res.length - 1) {
							arName = arName + res[i].name
						}
					};
					return arName
				}
			},
			setLength(res) {
				let min = "00";
				let sec = "00";
				min = parseInt(res / 60000);
				sec = parseInt((res % 60000) / 1000);
				if (min < 10) {
					min = "0" + min
				};
				if (sec < 10) {
					sec = "0" + sec
				};
				return min + ":" + sec
			}
		},
		beforeDestroy() {
			if (this.timer) {
				clearInterval(this.timer) // 在Vue实例销毁前，清除时间定时器
			}
		},
		components: {
			Comments
		}
	}
</script>

<style scoped>
    @import "../css/Player.css";
</style>