import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Hot from '../views/Hot.vue'
import Search from '../views/Search.vue'
import Player from '../views/Player.vue'
import Detail from '../views/Detail.vue'

Vue.use(VueRouter)
 
const routes = [
  {
    path: "",
    redirect: "/home"
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: "/hot",
    name: "Hot",
    component: Hot
  },
  {
    path: "/search",
    name: "Search",
    component: Search
  },
  {
    path: "/player",
    name: "Player",
    component: Player
  },
  {
    path: "/detail",
    name: "Detail",
    component: Detail
  }
]

const router = new VueRouter({
  routes,
  base: "/",
  mode: 'hash',
  linkActiveClass: "active"  //自定义处于活跃（选中）状态的元素类名
})

export default router
